import { Button } from "@/components/ui/button";
import { Card, CardContent } from "@/components/ui/card";
import { 
  Smartphone, 
  TrendingUp, 
  Shield, 
  BarChart3,
  ArrowLeft,
  CheckCircle,
  Star,
  Zap,
  Crown,
  Sparkles,
  LayoutDashboard,
  Monitor,
  Download
} from "lucide-react";
import BrandLogo from '../components/BrandLogo';
import { useEffect, useState } from 'react';
import { Link } from "react-router-dom";
import { useFirebaseAuth } from "@/contexts/FirebaseAuthContext";
import ProfileIcon from "@/components/ProfileIcon";
import NotificationBell from "@/components/NotificationBell";
import { useWhatsAppSettings, createWhatsAppLink } from '../utils/whatsappService';
import { getAppDownloads, getCachedOrEnvAppDownloads, listenAppDownloads, resolveDownloadUrl, type AppDownloads } from '@/utils/appDownloadService';

export default function LandingPage() {
  const { user } = useFirebaseAuth();
  const { settings } = useWhatsAppSettings();
  const [appDownloads, setAppDownloads] = useState<AppDownloads>(getCachedOrEnvAppDownloads());
  
  // دالة لإنشاء رابط الواتساب للاستفسار العام
  const createGeneralWhatsAppLink = () => {
    if (!settings) return '#';
    
    const message = `مرحباً! أريد الاستفسار عن خدمات CashyLink لإدارة المحافظ الإلكترونية.

أرجو التواصل معي لمعرفة المزيد عن الباقات المتاحة وكيفية البدء.`;
    
    return createWhatsAppLink(settings.phoneNumber, message);
  };

  useEffect(() => {
    // جلب أولي (مرة واحدة)
    (async () => {
      try {
        const data = await getAppDownloads();
        setAppDownloads(data || {});
      } catch (e) {
        console.error('Failed to load app downloads', e);
      }
    })();
    // متابعة لحظية لتحديث الروابط فور تعديلها من لوحة الأدمن
    const unsub = listenAppDownloads((data) => {
      setAppDownloads(data || {});
    });
    return () => {
      try { unsub(); } catch {}
    };
  }, []);
  
  const features = [
    {
      icon: Smartphone,
      title: "إدارة الخطوط والمحافظ",
      description: "تحكم كامل في خطوط المحل ومحافظ التجار مع تتبع الحدود اليومية والشهرية"
    },
    {
      icon: TrendingUp,
      title: "نظام عمولة مرن",
      description: "احسب العمولة تلقائياً حسب قواعد قابلة للتخصيص لكل مزود وتاجر"
    },
    {
      icon: Shield,
      title: "أمان متقدم",
      description: "حماية البيانات مع صلاحيات متدرجة وسجل مراجعة شامل"
    },
    {
      icon: BarChart3,
      title: "تقارير تفصيلية",
      description: "تقارير يومية وشهرية قابلة للتصدير مع رسوم بيانية تفاعلية"
    }
  ];

  const providers = [
    { name: "Vodafone Cash", color: "provider-vodafone" },
    { name: "Etisalat Cash", color: "provider-etisalat" },
    { name: "Orange Cash", color: "provider-orange" },
    { name: "InstaPay", color: "provider-instapay" }
  ];

  return (
    <div className="dark min-h-screen bg-background" data-theme="dark">
      {/* Header */}
      <header className="container mx-auto px-4 py-4">
        <nav className="flex items-center justify-between">
          <div className="flex items-center gap-2">
            <BrandLogo size={32} className="gradient-primary rounded-lg flex items-center justify-center" />
            <span className="text-xl font-bold">CashyLink</span>
          </div>
          <div className="flex items-center gap-4">
            <Link to="/privacy-policy" className="text-sm text-muted-foreground hover:text-primary underline-offset-4 hover:underline">سياسة الخصوصية</Link>
            {!user ? (
              <>
                <Link to="/user/login">
                  <Button variant="ghost">تسجيل الدخول</Button>
                </Link>
                <Link to="/user/register">
                  <Button className="btn-primary">
                    تسجيل تاجر جديد
                    <ArrowLeft className="mr-2 h-4 w-4" />
                  </Button>
                </Link>
              </>
            ) : (
              <>
                <Link to="/user/dashboard">
                  <Button className="btn-primary">
                    <LayoutDashboard className="ml-2 h-4 w-4" />
                    لوحة التحكم
                  </Button>
                </Link>
                {/* تم إخفاء أزرار الجرس والبروفايل من الصفحة الرئيسية بناءً على طلب المستخدم */}
              </>
            )}
          </div>
        </nav>
      </header>

      {/* Hero Section */}
      <section className="container mx-auto px-4 py-12 text-center relative overflow-hidden">
        {/* Background Animation */}
        <div className="absolute inset-0 -z-10">
          <div className="absolute top-12 left-10 w-72 h-72 bg-blue-400/20 rounded-full blur-3xl animate-pulse"></div>
          <div className="absolute bottom-20 right-10 w-96 h-96 bg-purple-400/20 rounded-full blur-3xl animate-pulse" style={{animationDelay: '1s'}}></div>
          <div className="absolute top-1/2 left-1/2 transform -translate-x-1/2 -translate-y-1/2 w-80 h-80 bg-green-400/10 rounded-full blur-3xl animate-pulse" style={{animationDelay: '2s'}}></div>
        </div>

        <div className="max-w-4xl mx-auto relative">
          {/* Floating Icons */}
          <div className="absolute -top-10 -left-10 animate-bounce" style={{animationDelay: '0.5s'}}>
            <Sparkles className="h-8 w-8 text-yellow-400" />
          </div>
          <div className="absolute -top-5 -right-5 animate-bounce" style={{animationDelay: '1.5s'}}>
            <Star className="h-6 w-6 text-blue-400" />
          </div>
          <div className="absolute top-12 -right-20 animate-bounce" style={{animationDelay: '2.5s'}}>
            <Crown className="h-10 w-10 text-purple-400" />
          </div>

          <div className="animate-fade-in-up">
            <h1 className="text-6xl md:text-7xl font-bold mb-6 gradient-hero bg-clip-text text-transparent leading-tight">
              منصة إدارة شاملة
              <br />
              <span className="text-5xl md:text-6xl">لمحلات الموبايل</span>
            </h1>
          </div>
          
          <div className="animate-fade-in-up" style={{animationDelay: '0.3s'}}>
            <p className="text-xl md:text-2xl text-muted-foreground mb-8 leading-relaxed max-w-3xl mx-auto">
              أدر معاملات الأموال الإلكترونية بكفاءة وأمان مع تتبع دقيق للحدود والعمولات
            </p>
          </div>

          <div className="animate-fade-in-up mb-12" style={{animationDelay: '0.6s'}}>
            <div className="flex flex-wrap justify-center gap-4">
              {providers.map((provider, index) => (
                <div 
                  key={provider.name}
                  className={`px-6 py-3 ${provider.color} text-white rounded-full text-sm font-medium shadow-lg transform hover:scale-105 transition-all duration-300 animate-slide-in-right`}
                  style={{animationDelay: `${0.8 + index * 0.1}s`}}
                >
                  {provider.name}
                </div>
              ))}
            </div>
          </div>

          <div className="animate-fade-in-up" style={{animationDelay: '1.2s'}}>
            <div className="flex flex-col sm:flex-row gap-6 justify-center items-center">
              <Link to="/subscriptions">
                <Button size="lg" className="btn-primary shadow-elegant transform hover:scale-105 transition-all duration-300 group relative overflow-hidden">
                  <span className="absolute inset-0 bg-gradient-to-r from-yellow-400/20 to-orange-400/20 transform scale-x-0 group-hover:scale-x-100 transition-transform duration-300 origin-left"></span>
                  <Crown className="ml-2 h-5 w-5 group-hover:animate-pulse" />
                  اشترك الآن
                  <Sparkles className="mr-2 h-5 w-5 group-hover:animate-spin" />
                </Button>
              </Link>
            </div>
            {/* Download icons */}
            <div className="mt-8 flex flex-col sm:flex-row gap-6 justify-center items-center">
              <a
                href={resolveDownloadUrl(appDownloads?.androidUrl) || '#'}
                onClick={(e) => { if (!appDownloads?.androidUrl) e.preventDefault(); }}
                className={`flex items-center gap-3 px-6 py-3 rounded-xl shadow-elegant transform hover:scale-105 transition-all duration-300 ${appDownloads?.androidUrl ? 'bg-green-600 text-white hover:bg-green-700' : 'bg-gray-300 text-gray-600 cursor-not-allowed'}`}
                aria-disabled={!appDownloads?.androidUrl}
                target="_blank"
                rel="noopener noreferrer"
              >
                <Smartphone className="h-5 w-5" />
                تحميل للأندرويد
                <Download className="h-4 w-4" />
              </a>
              <a
                href={resolveDownloadUrl(appDownloads?.pcUrl) || '#'}
                onClick={(e) => { if (!appDownloads?.pcUrl) e.preventDefault(); }}
                className={`flex items-center gap-3 px-6 py-3 rounded-xl shadow-elegant transform hover:scale-105 transition-all duration-300 ${appDownloads?.pcUrl ? 'bg-indigo-600 text-white hover:bg-indigo-700' : 'bg-gray-300 text-gray-600 cursor-not-allowed'}`}
                aria-disabled={!appDownloads?.pcUrl}
                target="_blank"
                rel="noopener noreferrer"
              >
                <Monitor className="h-5 w-5" />
                تحميل تطبيق الكمبيوتر نسخة 64 بت
                <Download className="h-4 w-4" />
              </a>
              <a
                href={resolveDownloadUrl(appDownloads?.pc32Url) || '#'}
                onClick={(e) => { if (!appDownloads?.pc32Url) e.preventDefault(); }}
                className={`flex items-center gap-3 px-6 py-3 rounded-xl shadow-elegant transform hover:scale-105 transition-all duration-300 ${appDownloads?.pc32Url ? 'bg-blue-600 text-white hover:bg-blue-700' : 'bg-gray-300 text-gray-600 cursor-not-allowed'}`}
                aria-disabled={!appDownloads?.pc32Url}
                target="_blank"
                rel="noopener noreferrer"
              >
                <Monitor className="h-5 w-5" />
                تحميل تطبيق الكمبيوتر نسخة 32 بت
                <Download className="h-4 w-4" />
              </a>
            </div>
          </div>

          {/* Stats Section */}
          <div className="mt-16 animate-fade-in-up" style={{animationDelay: '1.5s'}}>
            <div className="grid grid-cols-1 md:grid-cols-3 gap-8 max-w-2xl mx-auto">
              <div className="text-center group">
                <div className="text-3xl font-bold text-primary group-hover:scale-110 transition-transform duration-300">500+</div>
                <div className="text-muted-foreground">تاجر نشط</div>
              </div>
              <div className="text-center group">
                <div className="text-3xl font-bold text-primary group-hover:scale-110 transition-transform duration-300">1M+</div>
                <div className="text-muted-foreground">معاملة شهرياً</div>
              </div>
              <div className="text-center group">
                <div className="text-3xl font-bold text-primary group-hover:scale-110 transition-transform duration-300">99.9%</div>
                <div className="text-muted-foreground">وقت التشغيل</div>
              </div>
            </div>
          </div>
        </div>
      </section>

      {/* Features Section */}
      <section className="container mx-auto px-4 py-20 relative">
        <div className="text-center mb-16 animate-fade-in-up">
          <div className="inline-flex items-center gap-2 bg-primary/10 text-primary px-4 py-2 rounded-full text-sm font-medium mb-4">
            <Zap className="h-4 w-4" />
            مميزات استثنائية
          </div>
          <h2 className="text-4xl md:text-5xl font-bold mb-4 gradient-hero bg-clip-text text-transparent">
            مميزات المنصة
          </h2>
          <p className="text-muted-foreground text-xl max-w-2xl mx-auto">
            كل ما تحتاجه لإدارة عمليات التحويل بكفاءة وأمان تام
          </p>
        </div>
        
        <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-8">
          {features.map((feature, index) => (
            <Card 
              key={index} 
              className="card-glass hover:shadow-elegant transition-all duration-500 group relative overflow-hidden animate-fade-in-up border-0 bg-gradient-to-br from-white/80 to-white/40 backdrop-blur-sm"
              style={{animationDelay: `${index * 0.1}s`}}
            >
              <div className="absolute inset-0 bg-gradient-to-br from-primary/5 to-transparent opacity-0 group-hover:opacity-100 transition-opacity duration-300"></div>
              <CardContent className="p-8 text-center relative z-10">
                <div className="relative mb-6">
                  <div className="h-16 w-16 gradient-primary rounded-2xl flex items-center justify-center mx-auto group-hover:scale-110 group-hover:rotate-6 transition-all duration-300 shadow-lg">
                    <feature.icon className="h-8 w-8 text-white" />
                  </div>
                  <div className="absolute -top-2 -right-2 w-6 h-6 bg-yellow-400 rounded-full opacity-0 group-hover:opacity-100 group-hover:animate-ping transition-all duration-300"></div>
                </div>
                <h3 className="text-xl font-bold mb-3 group-hover:text-primary transition-colors duration-300">
                  {feature.title}
                </h3>
                <p className="text-muted-foreground leading-relaxed">
                  {feature.description}
                </p>
              </CardContent>
            </Card>
          ))}
        </div>

        {/* Floating Elements */}
        <div className="absolute top-10 left-10 w-20 h-20 bg-blue-400/10 rounded-full blur-xl animate-float"></div>
        <div className="absolute bottom-10 right-10 w-32 h-32 bg-purple-400/10 rounded-full blur-xl animate-float" style={{animationDelay: '2s'}}></div>
      </section>

      {/* Benefits Section */}
      <section className="container mx-auto px-4 py-20 my-20 relative">
        <div className="bg-gradient-to-br from-primary/5 via-purple-500/5 to-blue-500/5 rounded-3xl p-8 md:p-12 relative overflow-hidden">
          {/* Background Pattern */}
          <div className="absolute inset-0 opacity-10">
            <div className="absolute top-0 left-0 w-40 h-40 bg-primary rounded-full blur-3xl"></div>
            <div className="absolute bottom-0 right-0 w-60 h-60 bg-blue-500 rounded-full blur-3xl"></div>
          </div>

          <div className="grid grid-cols-1 lg:grid-cols-2 gap-12 items-center relative z-10">
            <div className="animate-fade-in-up">
              <div className="inline-flex items-center gap-2 bg-primary/10 text-primary px-4 py-2 rounded-full text-sm font-medium mb-6">
                <Star className="h-4 w-4" />
                لماذا نحن الأفضل؟
              </div>
              <h2 className="text-4xl md:text-5xl font-bold mb-8 gradient-hero bg-clip-text text-transparent">
                لماذا CashyLink؟
              </h2>
              <div className="space-y-6">
                {[
                  "تتبع دقيق للحدود اليومية والشهرية",
                  "تنبيهات تلقائية عند اقتراب الحدود",
                  "حساب عمولة ذكي ومرن",
                  "تقارير شاملة قابلة للتصدير",
                  "واجهات منفصلة للإدارة والتجار",
                  "أمان متقدم مع صلاحيات متدرجة"
                ].map((benefit, index) => (
                  <div 
                    key={index} 
                    className="flex items-center gap-4 group animate-slide-in-left"
                    style={{animationDelay: `${index * 0.1}s`}}
                  >
                    <div className="h-8 w-8 bg-gradient-to-r from-green-400 to-blue-500 rounded-full flex items-center justify-center group-hover:scale-110 transition-transform duration-300">
                      <CheckCircle className="h-5 w-5 text-white" />
                    </div>
                    <span className="text-foreground text-lg group-hover:text-primary transition-colors duration-300">
                      {benefit}
                    </span>
                  </div>
                ))}
              </div>
            </div>
            
            <div className="relative animate-fade-in-up" style={{animationDelay: '0.3s'}}>
              <div className="gradient-primary rounded-3xl p-8 shadow-2xl transform hover:scale-105 transition-all duration-500 relative overflow-hidden">
                {/* Animated Background */}
                <div className="absolute inset-0 bg-gradient-to-r from-yellow-400/20 to-orange-400/20 animate-pulse"></div>
                
                <div className="bg-white/10 backdrop-blur-sm rounded-2xl p-8 text-white relative z-10">
                  <div className="flex items-center gap-3 mb-6">
                    <div className="h-12 w-12 bg-white/20 rounded-full flex items-center justify-center">
                      <Zap className="h-6 w-6 text-white" />
                    </div>
                    <h3 className="text-2xl font-bold">ابدأ في دقائق</h3>
                  </div>
                  
                  <p className="text-white/90 mb-8 text-lg leading-relaxed">
                    سجل حسابك، أضف محافظك، وابدأ في إجراء المعاملات فوراً مع أحدث التقنيات
                  </p>
                  
                  <div className="space-y-4 mb-8">
                    <div className="flex items-center gap-3">
                      <div className="h-3 w-3 bg-white rounded-full animate-pulse"></div>
                      <span className="text-white/80">تسجيل سريع وآمن</span>
                    </div>
                    <div className="flex items-center gap-3">
                      <div className="h-3 w-3 bg-white/60 rounded-full animate-pulse" style={{animationDelay: '0.2s'}}></div>
                      <span className="text-white/80">إعداد المحافظ</span>
                    </div>
                    <div className="flex items-center gap-3">
                      <div className="h-3 w-3 bg-white/40 rounded-full animate-pulse" style={{animationDelay: '0.4s'}}></div>
                      <span className="text-white/80">بدء المعاملات</span>
                    </div>
                  </div>

                  <Link to="/subscriptions">
                    <Button className="w-full bg-white text-primary hover:bg-white/90 font-bold py-3 transform hover:scale-105 transition-all duration-300">
                      <Crown className="ml-2 h-5 w-5" />
                      اشترك الآن واحصل على مميزات حصرية
                    </Button>
                  </Link>
                </div>
              </div>
            </div>
          </div>
        </div>
      </section>

      {/* CTA Section */}
      <section className="container mx-auto px-4 py-20 relative">
        <div className="gradient-primary rounded-3xl p-12 text-center text-white shadow-2xl relative overflow-hidden">
          {/* Animated Background Elements */}
          <div className="absolute inset-0 opacity-20">
            <div className="absolute top-10 left-10 w-20 h-20 bg-white rounded-full animate-bounce" style={{animationDelay: '0s'}}></div>
            <div className="absolute top-20 right-20 w-16 h-16 bg-yellow-300 rounded-full animate-bounce" style={{animationDelay: '0.5s'}}></div>
            <div className="absolute bottom-20 left-20 w-12 h-12 bg-blue-300 rounded-full animate-bounce" style={{animationDelay: '1s'}}></div>
            <div className="absolute bottom-10 right-10 w-24 h-24 bg-green-300 rounded-full animate-bounce" style={{animationDelay: '1.5s'}}></div>
          </div>

          {/* Floating Icons */}
          <div className="absolute inset-0 pointer-events-none">
            <Star className="absolute top-16 left-1/4 h-6 w-6 text-yellow-300 animate-pulse" />
            <Crown className="absolute top-32 right-1/3 h-8 w-8 text-yellow-400 animate-pulse" style={{animationDelay: '0.3s'}} />
            <Sparkles className="absolute bottom-24 left-1/3 h-7 w-7 text-blue-300 animate-pulse" style={{animationDelay: '0.6s'}} />
            <Zap className="absolute bottom-16 right-1/4 h-6 w-6 text-green-300 animate-pulse" style={{animationDelay: '0.9s'}} />
          </div>

          <div className="relative z-10">
            <div className="inline-flex items-center gap-2 bg-white/20 backdrop-blur-sm px-6 py-3 rounded-full text-sm font-medium mb-8 animate-fade-in-up">
              <Crown className="h-5 w-5 text-yellow-300" />
              عرض محدود - اشترك الآن!
            </div>

            <h2 className="text-5xl md:text-6xl font-bold mb-8 animate-fade-in-up" style={{animationDelay: '0.2s'}}>
              جاهز لتحويل إدارة محافظك؟
            </h2>
            
            <p className="text-xl md:text-2xl text-white/90 mb-12 max-w-3xl mx-auto leading-relaxed animate-fade-in-up" style={{animationDelay: '0.4s'}}>
              انضم إلى آلاف التجار الذين يثقون في CashyLink لإدارة معاملاتهم المالية بكفاءة وأمان
            </p>

            {/* Stats */}
            <div className="grid grid-cols-1 md:grid-cols-3 gap-8 mb-12 animate-fade-in-up" style={{animationDelay: '0.6s'}}>
              <div className="text-center">
                <div className="text-3xl font-bold text-yellow-300 mb-2">5000+</div>
                <div className="text-white/80">تاجر نشط</div>
              </div>
              <div className="text-center">
                <div className="text-3xl font-bold text-blue-300 mb-2">99.9%</div>
                <div className="text-white/80">وقت التشغيل</div>
              </div>
              <div className="text-center">
                <div className="text-3xl font-bold text-green-300 mb-2">24/7</div>
                <div className="text-white/80">دعم فني</div>
              </div>
            </div>
            
            <div className="flex flex-col sm:flex-row gap-6 justify-center animate-fade-in-up" style={{animationDelay: '0.8s'}}>
              <Link to="/subscriptions">
                <Button size="lg" className="bg-white text-primary hover:bg-white/90 font-bold px-8 py-4 text-lg transform hover:scale-105 transition-all duration-300 shadow-xl">
                  <Crown className="ml-2 h-6 w-6" />
                  اشترك الآن - عرض خاص
                </Button>
              </Link>
              
              <Link to="/auth?mode=signup">
                <Button size="lg" variant="outline" className="border-2 border-white text-white hover:bg-white hover:text-primary font-bold px-8 py-4 text-lg transform hover:scale-105 transition-all duration-300">
                  إنشاء حساب مجاني
                  <ArrowLeft className="mr-2 h-6 w-6" />
                </Button>
              </Link>
            </div>

            {/* Trust Indicators */}
            <div className="mt-12 flex flex-wrap justify-center items-center gap-8 text-white/60 animate-fade-in-up" style={{animationDelay: '1s'}}>
              <div className="flex items-center gap-2">
                <CheckCircle className="h-5 w-5 text-green-300" />
                <span>آمن ومشفر</span>
              </div>
              <div className="flex items-center gap-2">
                <CheckCircle className="h-5 w-5 text-green-300" />
                <span>بدون رسوم خفية</span>
              </div>
              <div className="flex items-center gap-2">
                <CheckCircle className="h-5 w-5 text-green-300" />
                <span>إلغاء في أي وقت</span>
              </div>
            </div>
          </div>
        </div>
      </section>

      {/* Footer */}
      <footer className="border-t border-border bg-card/50 py-8">
        <div className="container mx-auto px-4 text-center">
          <div className="flex items-center justify-center gap-2 mb-4">
            <BrandLogo size={24} />
            <span className="font-semibold">CashyLink</span>
          </div>
          <div className="flex items-center justify-center gap-4 text-sm">
            <p className="text-muted-foreground">© 2024 CashyLink. جميع الحقوق محفوظة.</p>
            <Link to="/privacy-policy" className="text-muted-foreground hover:underline">سياسة الخصوصية</Link>
          </div>
        </div>
      </footer>
    </div>
  );
}